import { defineComponent, reactive } from "vue";
import { uuid } from "./utils/uuid";
import { getRandomImageSrc } from "./utils/random-image";
import { ElImage, ElButton } from "element-plus";
export default defineComponent({
    name: "App",
    components: {
        ElImage: ElImage,
        ElButton: ElButton,
    },
    setup: function () {
        var imageList = reactive(Array.from({ length: 1 }, function () {
            return {
                id: uuid(),
                name: uuid(),
                src: getRandomImageSrc(),
            };
        }));
        var handleAdd1 = function () {
            imageList.push({
                id: uuid(),
                name: uuid(),
                src: getRandomImageSrc(),
            });
        };
        var handleDel = function () {
            var length = imageList.length;
            var index = Math.floor(Math.random() * length);
            imageList.splice(index, 1);
        };
        return { imageList: imageList, handleAdd1: handleAdd1, handleDel: handleDel };
    },
});
